import { ItemInputDetail, ItemInfo, TagCloudItemProps } from './TagCloud.type';

/**
 * Returns the item based on item info in a tag cloud.
 * @param itemInfo
 * @param items
 * @returns
 */
function getItem<K>(itemInfo: ItemInfo, items: TagCloudItemProps<K>[]) {
  const { idx } = itemInfo;
  return items[idx];
}

export function getItemPadding(height: number | undefined) {
  return height != null ? 0.15 * height : 0;
}

/**
 * Returns the next item's info in a tag cloud.
 * @param itemInfo
 * @param items
 * @returns
 */
function getNextItemInfo<K>(itemInfo: ItemInfo, items: TagCloudItemProps<K>[]) {
  let idx = itemInfo.idx;

  idx = Math.min(items.length - 1, idx + 1);
  return { idx, id: items[idx].id };
}

/**
 * Returns the previous item's info in a tag cloud.
 * @param itemInfo
 * @param items
 * @returns
 */
function getPrevItemInfo<K>(itemInfo: ItemInfo, items: TagCloudItemProps<K>[]) {
  let idx = itemInfo.idx;

  idx = Math.max(0, idx - 1);
  return { idx, id: items[idx].id };
}

/**
 * Returns utils functions for navigation in a tag cloud.
 * @param items
 * @returns
 */
export function getTagCloudNavUtils<K>(items: TagCloudItemProps<K>[]) {
  return {
    getItem: (itemInfo: ItemInfo) => {
      return getItem(itemInfo, items);
    },
    getDetailFromInfo: (itemInfo?: ItemInfo): ItemInputDetail<K> => {
      if (!itemInfo) {
        return { id: undefined };
      }
      const item = getItem(itemInfo, items);
      return { id: item.id };
    },
    getPrevItemInfo: (itemInfo: ItemInfo) => {
      return getPrevItemInfo(itemInfo, items);
    },
    getNextItemInfo: (itemInfo: ItemInfo) => {
      return getNextItemInfo(itemInfo, items);
    }
  };
}
