import { TagCloudItem, TagItemProps } from './TagCloudItem';
import { TagCloudProps } from './TagCloud.type';
import { getAriaProps, getDatatipText, getItemProps, getLayout } from './TagCloudUtils';
import { getTagCloudNavUtils } from './TagCloudNavUtils';
import { useEvents } from './useEvents';
import { TrackResizeContainer } from '../PRIVATE_TrackResizeContainer';
import { useTagCloudDatatip } from './useTagCloudDatatip';
import { mergeProps } from '../utils/UNSAFE_mergeProps';
import { useLayoutEffect, useRef, useState } from 'preact/hooks';
import { useTranslationBundle } from '../hooks/UNSAFE_useTranslationBundle';
import type { BundleType } from '../resources/nls/bundle';
import { useTextDimensions } from '../hooks/PRIVATE_useTextDimensions';
import { useUser } from '../hooks/UNSAFE_useUser';
import { baseStyles } from './themes/TagCloudStyles.css';
import { useSelection } from '../hooks/PRIVATE_useVizSelection';
import { getItemInfo } from './TagCloudUtils';
import { Size, sizeToCSS } from '../utils/UNSAFE_size';

/**
 * Tag clouds are used to display text data with the importance of each tag shown with font size and/or color.
 */
export function TagCloud<K extends string | number>({
  selectionMode = 'none',
  layout = 'cloud',
  height = '112x',
  width = '100%',
  items,
  onItemAction,
  onItemInput,
  ...props
}: TagCloudProps<K>) {
  const rootRef = useRef<HTMLDivElement>(null);
  const focusedItemRef = useRef<TagItemProps<K> | null>(null);
  const translations = useTranslationBundle<BundleType>('@oracle/oraclejet-preact');

  const filteredItems = items.filter((i) => !props.hiddenIds?.includes(i.id));

  const hasData = filteredItems && filteredItems.length !== 0;
  const supportsSelection = !!(selectionMode && selectionMode !== 'none');
  const supportsAction = onItemAction != null;
  const isReadOnly = supportsAction && onItemInput === null;

  const ariaProps = getAriaProps(translations, hasData, props.accessibleLabel);

  const { getItem, getDetailFromInfo, getPrevItemInfo, getNextItemInfo } =
    getTagCloudNavUtils(filteredItems);

  const { hoveredItemInfo, focusedItemInfo, ...eventsProps } = useEvents<K>(
    !isReadOnly && hasData,
    getDetailFromInfo,
    getPrevItemInfo,
    getNextItemInfo,
    onItemAction,
    onItemInput
  );

  const datatipText = getDatatipText(
    filteredItems,
    getItem,
    focusedItemInfo,
    hoveredItemInfo,
    props.datatip
  );
  const { datatipContent, datatipProps } = useTagCloudDatatip({
    text: datatipText,
    rootRef,
    focusedItemInfo,
    focusedItemRef
  });

  const idExtracter = (event: Event) => {
    // return focused item for keyboard event.
    // eventsProps should have already updated the focused item
    const info =
      event.type === 'keyup' ? focusedItemInfo : getItemInfo(event.target as HTMLElement);
    return getDetailFromInfo(info).id;
  };

  const selectionProps = useSelection<K>({
    idExtracter,
    selection: props.selectedIds,
    selectionMode: selectionMode,
    onChange: props.onSelectionChange
  });

  const mergedProps = mergeProps(eventsProps, datatipProps, selectionProps);
  const { direction } = useUser();

  return (
    <TrackResizeContainer
      width={sizeToCSS(width as Size) || width || '0px'}
      height={sizeToCSS(height as Size) || height || '0px'}>
      {(width, height) => {
        const { textMeasureContent, getTextDimensions } = useTextDimensions();
        const [layoutTags, setLayoutTags] = useState<TagItemProps<K>[]>([]);

        useLayoutEffect(() => {
          if (!getTextDimensions || !hasData) return;
          setLayoutTags(
            getLayout(layout, filteredItems, width, height, direction === 'rtl', getTextDimensions)
          );
        }, [layout, filteredItems, width, height, direction, getTextDimensions]);

        return (
          <div
            ref={rootRef}
            tabIndex={isReadOnly ? undefined : 0}
            style={{ width, height }}
            class={baseStyles}
            {...ariaProps}
            {...mergedProps}>
            {layoutTags.map((item, itemIndex) => {
              const itemProps = getItemProps(
                item,
                itemIndex,
                supportsSelection,
                isReadOnly,
                hoveredItemInfo,
                props.selectedIds,
                focusedItemInfo,
                props.highlightedIds,
                eventsProps['aria-activedescendant']
              );
              const isFocusedItem = focusedItemInfo.idx === itemIndex;
              isFocusedItem ? (focusedItemRef.current = item) : null;
              return (
                <TagCloudItem
                  fontSize={item.fontSize}
                  x={item.x}
                  y={item.y}
                  accessibleLabel={item.accessibleLabel}
                  supportsSelection={supportsSelection}
                  supportsAction={supportsAction}
                  translations={translations}
                  {...itemProps}
                />
              );
            })}
            {textMeasureContent}
            {datatipContent}
          </div>
        );
      }}
    </TrackResizeContainer>
  );
}
